<template>
  <div class="left">
    <ul>
      <li v-for="(item,index) in leftArr" :key="item.id" :class="{active:index == curid}">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="item.img" />
        </svg>
        <router-link :to="item.url">{{ item.name }}</router-link>
      </li>
      <li :class="{active:curid == 6}" @click="pull">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-banner" />
        </svg>
        <div class="pull-btn">轮播图管理</div>
        <i class="el-icon-caret-bottom" :class="{pull:isPull}"></i>
        <div v-if="visible" class="other" :class="{changeHeight:isPull}">
          <p @click.stop="changeClassify(1)">首页轮播</p>
          <p @click.stop="changeClassify(2)">产品轮播</p>
        </div>
        <div v-else class="other" :class="{changeHeight:isPull}">
          <router-link :to="{path:'/banner', query: {id:1}}">首页轮播</router-link>
          <router-link :to="{path:'/banner', query: {id:2}}">产品轮播</router-link>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Left",
  data() {
    return {
      leftArr: [
        { name: "产品管理", img: "#icon-chanpin1", num: "0", url: "/product" },
        { name: "新闻管理", img: "#icon-xinwen", num: "1", url: "/news" },
        { name: "专利管理", img: "#icon-zhuanli", num: "2", url: "/patent" },
        { name: "荣誉管理", img: "#icon-rongyu", num: "3", url: "/honor" },
        { name: "活动管理", img: "#icon-huodong", num: "4", url: "/activity" }
      ]
    };
  },
  props: ["curid", "isPull", "visible"],
  methods: {
    pull() {
      this.$emit("pull");
    },
    changeClassify(index) {
      this.$emit("change", index);
    }
  }
};
</script>