<template>
  <div class="header">
    <p>人信官网管理系统</p>
    <div class="header-right">
      <ul>
        <li @click="reset">
          <i class="el-icon-refresh"></i>
          <span>刷新</span>
        </li>
        <li @click="toLogin">
          <i class="el-icon-refresh-left"></i>
          <span>注销</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "HeaderContent",
  data() {
    return {};
  },
  props: [],
  methods: {
    reset() {
      this.$router.go(0);
    },
    toLogin() {
      sessionStorage.removeItem("cookie")
      this.setCookie("mycookid", ' ', -1);
      this.$router.push("/");
    },
    setCookie(name, value, day) {
      if (day !== 0) {
        //当设置的时间等于0时，不设置expires属性，cookie在浏览器关闭后删除
        var expires = day * 24 * 60 * 60 * 1000;
        var date = new Date(+new Date() + expires);
        document.cookie = name + "=" + value + ";expires=" + date.toUTCString()+'; domain=192.168.10.75; path=/';
      } else {
        document.cookie = name + "=" + value;
      }
    }
  }
};
</script>