<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <!-- 头部 -->
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>
                <router-link to="/news">轮播图管理</router-link>
              </span> /
              <span>修改轮播图</span>
            </p>
          </div>
          <!-- 详情 -->
          <div class="info">
            <el-form :model="info" ref="info" label-width="180px" class="pro-info">
              <el-form-item label="轮播图标题：" prop="name">
                <el-input v-model="info.name"></el-input>
              </el-form-item>

              <el-form-item label="轮播图：" prop="img">
                <el-upload
                  class="banner-uploader"
                  name="imgfile"
                  :action="RespondURL.url+'Goodsparticulars/uploadimg'"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  <div v-if="imageUrl" class="delImg" @click.stop="delImg">
                    <i class="el-icon-circle-close"></i>
                  </div>
                  <div v-if="imageUrl" class="revise">
                    替换
                    <i class="el-icon-copy-document"></i>
                  </div>
                </el-upload>
              </el-form-item>
            </el-form>

            <div class="btn">
              <el-button type="primary" @click="submitForm">立即发送</el-button>
              <el-button @click="resetForm('info')">取消重置</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "BannerAdd",  
  components: { Left, HeaderContent },
  created() {
    let num = this.$route.query.id;
    this.classify = this.$route.query.classify;
    this.info = this.$store.state.arr[num];
    this.imageUrl = this.RespondURL.url + this.$store.state.arr[num].img;
    console.log(this.info)
  },
  data() {
    return {
      curid: "6",
      classify: "", //分类
      info: {},
      imageUrl: "",
      isPull: false
    };
  },
  methods: {
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    // 上传图片
    handleAvatarSuccess(res) {
      this.imageUrl = this.RespondURL.url + res.result;
      this.info.img = res.result;
    },
    // 发送
    submitForm() {
      this.$refs.info.validate(valid => {
        if (valid) {
          let obj = {
            id: parseInt(this.info.id),
            img: this.info.img,
            name:this.info.name
          };
          console.log( obj )
          let url = "";
          if (this.classify == 1) {
            url = this.RespondURL.url + "Slideshow/updateslideshow";
          } else {
            url = this.RespondURL.url + "GoodsCarousel/updateslideshow";
          }
          this.$ajax.post(url, obj).then(data => {
            console.log(data)
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {
                if (data.data.flag) {
                  this.$router.push({
                    path: "/banner",
                    query: {
                      id: this.classify
                    }
                  });
                }
              }
            });
          });
        } else {
          this.$alert("发送失败，请内容填写完整", "提示", {
            confirmButtonText: "确定",
            callback: () => {}
          });
          return false;
        }
      });
    },
    // 重置
    resetForm() {
      this.info = {
        id: "",
        img: ""
      };
      this.imageUrl = "";
    },
    delImg(){
      this.imageUrl='';
    }
  }
};
</script>
<style lang="less">
.banner-uploader {
  .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    // overflow: hidden;
    &:hover {
      border-color: #409eff;
      .delImg {
        display: block;
      }
      .revise {
        display: block;
      }
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 500px;
      height: 300px;
      line-height: 300px;
      text-align: center;
    }
    .delImg {
      width: 30px;
      height: 30px;
      position: absolute;
      overflow: hidden;
      right: -15px;
      top: -15px;
      z-index: 1999;
      background: rgb(112, 111, 111);
      border-radius: 50%;
      i {
        font-size: 30px;
        position: absolute;
        color: white;
        left: 0;
        top: 0;
      }
    }
    .avatar {
      width: 500px;
      height: 300px;
      display: block;
    }
    .revise {
      width: 500px;
      height: 300px;
      display: none;
      line-height: 300px;
      text-align: center;
      font-size: 18px;
      background: rgba(49, 49, 49, 0.5);
      color: white;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}
</style>